@value makerWhite, makerOverlay, makerDarkGray, bgBlack from '../../common/colors.css';

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: makerOverlay;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 99;
}

.container {
  position: relative;
  width: 600px;
  height: 200px;
  background-color: makerWhite;
  border-radius: 1em;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 1em;
}

.container.dark {
  background-color: bgBlack;
}

.form {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  border-top: 1px solid makerDarkGray;
  border-left: 1px solid makerDarkGray;
}

.input,
.textarea,
.select,
.button,
.fileInput {
  font-size: 0.8rem;
  width: 100%;
  border: 0;
  padding: 0.5em;
  border-bottom: 1px solid makerDarkGray;
  border-right: 1px solid makerDarkGray;
  background: transparent;
  color: inherit;
}

.input,
.select {
  flex: 1 1 30%;
}

.option {
  background: makerWhite;
  color: inherit;
}

.option.dark {
  background: bgBlack;
}

.fileInput {
  padding: 0;
  flex: 1 1 50%;
}

.input:focus,
.textarea:focus,
.select:focus,
.button:focus {
  outline: 0;
}

.textarea {
  flex-basis: 100%;
}
